import group from "../controllers/group";
export default (app) => {

    /* Routes for group */

    app.route("/group/addGroup").post(group.addGroup);

    app.route("/group/getAllGroup").get(group.getAllGroup);

    app.route("/group/updateGroup").put(group.updateGroup);

    app.route("/group/deleteGroup/:idGroup").delete(group.deleteGroup);

    return app;
};